.header {
    height: 90px;
    z-index: 1;
    .headerContainer {
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
    }
}

.non-animate {
    background-color: rgba(255, 255, 255, 0);
}

.animate {
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0 3px 4px rgb(0 0 0 / 20%);
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%
}

