@import '../../common/styles/Variables';

.aboutContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 80px auto;

  .AboutInfo {
    font-size: 21px;
    font-weight: 400;
    line-height: 1.8;
    margin-bottom: 16px;
    margin-top: 0;
  }

  .aboutInfoBlock {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 40px;

    .block {
      .title {
        font-weight: 600;
        color: $secondaryTitleColor;
        line-height: 1.8;
      }

      .info {
        font-weight: 600;
        font-size: 18px;
        line-height: 1.8;
      }
    }
  }
}









